document.addEventListener("DOMContentLoaded", () => {
	//? Includes
	//* // Variables
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

const body = document.querySelector('.body');
const headerContainer = document.querySelector('.header__container');
const burgerBtn = document.querySelector('.burger');
const nav = document.querySelector('.nav');
const mediaMobile = window.matchMedia("(max-width: 767px)");
const header = document.querySelector('.header');
const hero = document.querySelector('.hero');
const swiperSimpleSelector = document.querySelector(".simple__swiper");
const swiperPlatformSelector = document.querySelector('.simple__swiper--platform');
;
	//* // scrollLockIOS

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
};
	//* // Burger Menu
let menuIsOpen = false;

if (mediaMobile.matches) {
	nav.setAttribute('aria-hidden', true);
}

const burgerMenuOpen = () => {
	burgerBtn.classList.add('burger--active');
	body.classList.add('lock');
	nav.classList.add('nav--active')
	burgerBtn.setAttribute('aria-expanded', true);
	nav.setAttribute('aria-hidden', false);
	if (isiPad || isiPhone || isiPod) {
		disableScroll();
	}
	menuIsOpen = true;
}

const burgerMenuClose = () => {
	burgerBtn.classList.remove('burger--active');
	body.classList.remove('lock');
	nav.classList.remove('nav--active')
	burgerBtn.setAttribute('aria-expanded', false);
	nav.setAttribute('aria-hidden', true);
	if (isiPad || isiPhone || isiPod) {
		enableScroll();
	}
	menuIsOpen = false;
}

const burgerMenuSwitch = () => {
	switch (menuIsOpen) {
		case false:
			burgerMenuOpen();
			break;
		case true:
			burgerMenuClose();
			break;
	}
}

headerContainer.addEventListener('click', (e) => {
	if (e.target.classList.contains('burger')) {
		burgerMenuSwitch();
	} else if (e.target.classList.contains('nav__link')) {
		burgerMenuClose();
	}
})
;
	//* // Header Scroll
window.addEventListener('scroll', () => {
	headerFixed();
});
const headerFixed = () => {
	if (!mediaMobile.matches) {
		if (window.scrollY > 0) {
			header.classList.add('header--fixed');
		} else if (window.scrollY === 0) {
			header.classList.remove('header--fixed');
		}
	} else if (mediaMobile.matches && window.scrollY > 0) {
		header.classList.add('header--fixed');
	}
}
headerFixed();;
	//* // swiperMobile
let swiperSimpleMobileInit = () => {
	if (window.innerWidth <= 768 && swiperSimpleSelector.dataset.mobile === "false") {
		swiperSimple = new Swiper(swiperSimpleSelector, {
			containerModifierClass: "swiper",
			wrapperClass: "swiper__wrapper",
			slideClass: "swiper__slide",
			loop: true,
			spaceBetween: 15,
			pagination: {
				el: ".swiper__pagination",
				clickable: true,
				bulletElement: 'button',
				bulletClass: 'swiper-pagination-bullet btn-reset'
			},
			a11y: {
				paginationBulletMessage: 'Перейти к слайду {{index}}',
			},
			breakpoints: {
				300: {
					slidesPerView: 1.3,
				},
				450: {
					slidesPerView: 1.8,
				},
			},
		});
		swiperSimpleSelector.dataset.mobile = "true";
	}
	if (window.innerWidth > 768) {
		swiperSimpleSelector.dataset.mobile = "false";
		if (swiperSimpleSelector.classList.contains('swiperinitialized')) {
			swiperSimple.destroy();
		}
	}
};
swiperSimpleMobileInit();


let swiperPlatformMobileInit = () => {
	if (window.innerWidth <= 768 && swiperPlatformSelector.dataset.mobile === "false") {
		swiperPlatform = new Swiper(swiperPlatformSelector, {
			containerModifierClass: "swiper",
			wrapperClass: "swiper__wrapper",
			slideClass: "swiper__slide",
			loop: true,
			spaceBetween: 15,
			pagination: {
				el: ".swiper__pagination",
				clickable: true,
				bulletElement: 'button',
				bulletClass: 'swiper-pagination-bullet btn-reset'
			},
			a11y: {
				paginationBulletMessage: 'Перейти к слайду {{index}}',
			},
			breakpoints: {
				300: {
					slidesPerView: 1.3,
				},
				450: {
					slidesPerView: 1.8,
				},
			},
		});
		swiperPlatformSelector.dataset.mobile = "true";
	}
	if (window.innerWidth > 768) {
		swiperPlatformSelector.dataset.mobile = "false";
		if (swiperPlatformSelector.classList.contains('swiperinitialized')) {
			swiperPlatform.destroy();
		}
	}
};
swiperPlatformMobileInit();

window.addEventListener("resize", () => {
	swiperSimpleMobileInit();
	swiperPlatformMobileInit();
});;
	//? -- //
});